.App {
  text-align: center;
}
.card {
  display: flex;
  justify-content: space-between;
}
.card-display {
  width: 200px;
  padding: 20px;
  text-align: center;
  font-size: 1.5em;
}

.game {
  padding: 1em;
  margin-top: 5em;
  margin-right: -2em;
}

@media only screen and (max-width: 800px) {
  .card {
    display: block;
    margin: 5rem auto;
    background-color: rgba(253, 253, 253, 0.747);
  }
  .pokemone-data {
    display: block;
    margin: 0 auto;
    padding: 2rem;
  }
}
